@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;600;700&display=swap);
@font-face {
    font-family: Bahnschrift;
    src:url(/static/media/Bahnschrift.67863bca.ttf);
} 

body {
	padding:0;
	margin:0;
	font-family:'Bahnschrift';
}

label{
	-webkit-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
}

.header {
	width:100%;
	display:-webkit-flex;
	display:flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	height: 60px;
	background: cornflowerblue;
	-webkit-flex-direction: row;
	        flex-direction: row;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	color:white;
}

.dashboard {
	width:100%;
	display:-webkit-flex;
	display:flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	
	.designers-list{
		display:-webkit-flex;
		display:flex;
		-webkit-flex-direction: column;
		        flex-direction: column;
		height: 300px;
		width: 400px;
		overflow: hidden;
	}

	.list-item{
		display:-webkit-flex;
		display:flex;
		-webkit-flex-direction: row;
		        flex-direction: row;
		width: 100%;
		height: 50px;
		margin-top:5px;
		-webkit-justify-content: space-between;
		        justify-content: space-between;
		position: relative;
	}
}

